import Vue from 'vue'

const path = process.env.API_PATH
var previousRequest = null
export default {
  getDeals (params) {
    return Vue.http.get(path + '/bargains', {params: params,
      before (request) {
        if (previousRequest) {
          previousRequest.abort()
        }
        previousRequest = request
      }})
  },

  getStoreDeals (params) {
    return Vue.http.get(path + '/storedeals', {params: params,
      before (request) {
        if (previousRequest) {
          previousRequest.abort()
        }
        previousRequest = request
      }})
  },

  getSelectedBargains (params) {
    return Vue.http.post(path + '/bargains/search', params)
  },

  getTopDeals (params) {
    return Vue.http.get(path + '/bargains/best', {params: params,
      before (request) {
        if (previousRequest) {
          previousRequest.abort()
        }
        previousRequest = request
      }})
  },

  getLast7Days (params) {
    return Vue.http.get(path + '/bargains/last7d', {params: params,
      before (request) {
        if (previousRequest) {
          previousRequest.abort()
        }
        previousRequest = request
      }})
  },

  getTodayDeals (params) {
    return Vue.http.get(path + '/bargains/last24h', {params: params,
      before (request) {
        if (previousRequest) {
          previousRequest.abort()
        }
        previousRequest = request
      }})
  },

  getEditorPicks (params) {
    return Vue.http.get(path + '/bargains/editors-choice', {params: params,
      before (request) {
        if (previousRequest) {
          previousRequest.abort()
        }
        previousRequest = request
      }})
  },

  getStores () {
    return Vue.http.get(path + '/stores')
  },

  getDetails (id) {
    return Vue.http.get(path + '/bargains/' + id)
  },

  getEditorChoices () {
    return Vue.http.get(path + '/bargains/editors-choice')
  },
  getCategoryTree () {
    return Vue.http.get(path + '/categories/tree')
  },
  getCategory () {
    return Vue.http.get(path + '/categories')
  }
}



