<template>
  <div class="deal_body col-lg-9 col-md-12 col-xs-12 col-sm-12">
     <span id="top-link-block hidden-md-down z-in" class="text-center btn-fixed" :style="{'left': this.getOffset + 10 + 'px', 'bottom': '10px', 'display': (this.isScrolled) ? 'block' : 'none'}">
              <a href="" class="btn btn-secondary btn-sm btn-top" onclick="$('html,body').animate({scrollTop:0},'slow');return false;">
                  <i class="fa fa-arrow-up" aria-hidden="true"></i>
                  <p class="reset-col">Back To Top</p>
              </a>
          </span>
    <div class="feature-card col-lg-12" style="padding:0; margin: 0 0 5px 0; z-index: 999;">
      <nav class="navbar navbar-inverse dynamic-nav" data-toggle="sticky-onscroll"
           style="background-color: #fff; padding:0; box-shadow: 1px 1px 5px #ccc; padding-top:5px; padding-bottom:5px;">
        <div style="width:100%; vertical-align: middle;">
          <div class="clearfix" style="padding-left:0; vertical-align: middle; padding-right:10px; padding-left:10px;">
            <div class="d-inline-block dynamic-search col-lg-3 pull-left reset-col">
              <div class="input-group has-success">
                <el-input
                  icon="search"
                  placeholder="Enter keywords"
                  @change="onFilterChange"
                  v-model="filterInput"></el-input>
              </div>
            </div>


            <div class="d-inline-block pull-left price-range-filter hidden-sm-down in-cm-top" style="padding-left: 0px; padding-right:0;">
              <div class="row">
                <div class="col-lg-12 reset-col">
                  <div class="d-inline-block price-input" style="width:60px;">
                    <el-input
                      size="small"
                      placeholder="Min"
                      @change="onMinPriceChange"
                      v-model="minPrice">
                      <template slot="prepend">$</template>
                    </el-input>
                  </div>

                  <div class="d-inline-block price-input" style="width:60px; margin-left:7px;">
                    <el-input
                      size="small"
                      placeholder="Max"
                      @change="onMaxPriceChange"
                      v-model="maxPrice">
                      <template slot="prepend">$</template>
                    </el-input>
                  </div>
                </div>
              </div>
            </div>
            <div class="d-inline-block v-middle in-cm-top hidden-sm-down" style="width: 10em; margin-left: 10px;">
              <el-select v-model="checkedBargainType" @change="getBargainField" placeholder="Select">
                <el-option
                  v-for="item in bargainType"
                  :label="item.name"
                  :value="item.value">
                </el-option>
              </el-select>
            </div>

            <div class="d-inline-block price-range-filter hidden-sm-down hidden-cm-tool" style="margin-left: 5px; padding-right:0; width:125px;">
              <div class="row">
                <div class="col-lg-12 reset-col" style="padding:0">
                  <div class="d-inline-block v-middle" style="width:100%; margin-left:15px; max-width:250px; margin-top:-5px;">
                    <span style="margin-top:0; padding:0; font-size:11px;"><b>Min Discount: {{discountChange}}%</b></span>
                    <el-slider v-model="discountChange" @change="onDiscountChange" style="margin-top:8px;"></el-slider>
                  </div>
                </div>
              </div>
            </div>

            <div class="d-inline-block price-range-filter switch-pad-lelf" style="padding: 0; vertical-align: middle">
              <div class="row">
                <div class="col-lg-12 reset-col" style="padding:0;">
                  <div class="d-inline-block v-middle" style="width:100%; max-width:250px; ">
                    <el-switch @change="saveActive" class="btn-switch-cm" v-model="params.showOnlyActive" on-text="Active" off-text="All" on-color="#13ce66" off-color="#20A0FF" :width="60"></el-switch>
                  </div>
                </div>
              </div>
            </div>




            <div class="d-inline-block btn-group hidden-md-up pull-left in-cm-top-3 hidden-group-btn price-range-filter" style="padding-left: 0px; padding-right:0;">
              <div class="btn dropdown category-hover content-bar" style="margin: 0; padding: 0; font-size: 1em;">
                <button data-target="#dLabel" class="btn btn-raised dropdown-toggle btn-secondary categories-list" data-toggle="dropdown"
                        style="font-size:1em;  padding-top:9px; padding-bottom:8px;">Advance Filter
                </button>
                <div id="dLabel" class="dropdown-menu category-dropdown" style="min-width:16rem; z-index:9999;">
                  <div class="row">
                    <h6 class="col-lg-12" style="font-size:12px; margin-top:5px; margin-bottom:0;"><b>Price Range:</b>
                    </h6>
                    <div class="col-lg-12 ad-filter-pad-top">
                      <div class="d-inline-block price-input" style="width:50px; margin-top:3px;">
                        <el-input
                          size="small"
                          placeholder="Min"
                          @change="onMinPriceChange"
                          v-model="minPrice">
                          <template slot="prepend">$</template>
                        </el-input>
                      </div>
                      <div class="d-inline-block price-input" style="width:50px; margin-left:7px; margin-top:3px;">
                        <el-input
                          size="small"
                          placeholder="Max"
                          @change="onMaxPriceChange"
                          v-model="maxPrice">
                          <template slot="prepend">$</template>
                        </el-input>
                      </div>
                    </div>
                  </div>
                  <div class="row ad-filter-pad-top">
                    <h6 class="col-lg-12" style="font-size:12px; margin-top:5px; margin-bottom:0;">
                      <b>Min Discount: {{discountHiddenChange}}%</b></h6>
                    <div class="col-lg-12">
                      <div class="d-inline-block v-middle" style="width:100%; margin-left:5px; max-width:250px;">
                        <el-slider v-model="discountHiddenChange" @change="onDiscountHiddenChange"></el-slider>
                      </div>
                    </div>
                  </div>
                  <div class="row ad-filter-pad-top">
                    <h6 class="col-lg-12" style="font-size:12px; margin-top:5px; margin-bottom:0;">
                      <b>Min Rating: {{ratingChange}}</b></h6>
                    <div class="col-lg-12">
                      <div class="d-inline-block v-middle" style="width:100%; margin-left:5px; max-width:250px;">
                        <el-slider v-model="ratingChange" @change="onRatingChange" :step="0.1" :max="5"></el-slider>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="btn dropdown category-hover content-bar" style="margin: 0; padding: 0; font-size: 1em;">
                <button data-target="#dLabel1" class="btn btn-raised dropdown-toggle btn-secondary categories-list" data-toggle="dropdown"
                        style="font-size:1em; padding-top:9px; padding-bottom:8px;">Bargain Type
                </button>
                <div id="dLabel1" class="dropdown-menu category-dropdown" style="min-width:13rem; z-index:9999;">
                  <div class="row">
                    <div class="pure-radiobutton dropdown-item" v-for="field in bargainType">
                      <input type="radio" v-model="checkedBargainType" name="checkbox" :id="field.value"
                             :value="field.value">
                      <label :for="field.value" @click="getBargainType(field)" class="reset-col"
                             style="font-size:12px;">{{field.name}}</label>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <div class="d-inline-block pull-right in-cm-top-5" style="font-size:14px; ">
                <div class="d-inline-block reset-col" style="width: auto;">
                  <span style="color:#a0a0a0; font-size: 1.55em; cursor:pointer;" v-on:click="toggleLayout('grid')"
                        v-bind:class="{grid: this.toggle === 'grid'}"><i class="fa fa-th" aria-hidden="true"></i></span>
                  <span style="color:#a0a0a0; font-size: 1.55em; padding-left:10px; cursor:pointer;"
                        v-on:click="toggleLayout('list')"
                        v-bind:class="{grid: this.toggle === 'list'}"><i class="fa fa-th-list"
                                                                         aria-hidden="true"></i></span>
                  <span style="color:#a0a0a0; font-size: 1.55em; padding-left:10px; cursor:pointer;"
                        v-on:click="toggleLayout('feed')"
                        v-bind:class="{grid: this.toggle === 'feed'}"><i class="fa fa-list-alt"
                                                                         aria-hidden="true"></i></span>
                </div>
            </div>
          </div>
        </div>
      </nav>
    </div>
    <router-view :param="params"></router-view>
  </div>
</template>
<style>


</style>
<script>
  import apiService from '../../util/apiService'
  import {categoryFormat} from '../../util/filter'
  var $ = window.jQuery
  import _ from 'lodash'
  export default {
    name: 'AppContent2',
    data () {
      return {
        offsetWidth: 0,
        scrolled: false,
        active: false,
        swiperOption: {
          paginationClickable: true,
          direction: 'vertical',
          spaceBetween: 0
        },
        discountChange: 0,
        ratingChange: 0,
        discountHiddenChange: 0,
        checkedCategory: [],
        checkedBargainType: '',
        toggle: window.localStorage.getItem('layout') || 'grid',
        priceRange: [0, 10000],
        minPrice: null,
        max: 10000,
        minRating: 0,
        maxPrice: null,
        minDiscount: 0,
        filterInput: '',
        params: {
          keywords: '',
          categories: [],
          type: '',
          minSalePrice: 0,
          maxSalePrice: 0,
          minRating: 0,
          minDiscount: 0,
          showOnlyActive: (window.localStorage.getItem('active') && window.localStorage.getItem('active') === 'false') ? false : true
        },
        opacity: {
          opacity: '0.2'
        },
        category: [],
        storeList: [],
        deals: [],
        bargainType: [
          {
            name: 'Sale',
            value: 'sale'
          },
          {
            name: 'Percent Off',
            value: 'percentOff'
          },
          {
            name: 'Free',
            value: 'free'
          },
          {
            name: 'Promotion',
            value: 'promotion'
          },
          {
            name: 'Other',
            value: 'other'
          }
        ],
        status: 'first'
      }
    },
    computed: {
      isScrolled: function() {
        if (this.scrolled) {
          if (window.scrollY > 100) {
            return true
          } else {
            return false
          }
        } else {
          return false
        }
      },
      getOffset () {
        return this.offsetWidth
      }
    },
    beforeRouteLeave (to, from ,next) {
      $(window).off('scroll.btn-top')
      $(window).off('resize.btn-top')
      $(window).off('scroll.sticky-onscroll resize.sticky-onscroll')
      next()
    },
    methods: {
      onRatingChange (value) {
        this.params.minRating = value
      },
      onDiscountChange (value) {
        this.params.minDiscount = value
        this.discountHiddenChange = value
      },
      onDiscountHiddenChange (value) {
        this.params.minDiscount = value
        this.discountChange = value
      },
      saveActive (active) {
        window.localStorage.setItem('active', active)
      },
      onFilterChange (input) {
        this.params.keywords = input
      },
      onPriceChange (price) {
        this.minPrice = price[0]
        this.maxPrice = price[1]
        this.params.minSalePrice = price[0]
        this.params.maxSalePrice = price[1]
      },
      onMinPriceChange (price) {
        price = parseInt(price)
        if (price) {
          if (price >= 0 && price <= this.priceRange[1]) {
            this.priceRange = [price, this.priceRange[1]]
            this.params.minSalePrice = price
          }
        } else {
          this.params.minSalePrice = null
        }
      },
      onMaxPriceChange (price) {
        price = parseInt(price)
        if (price) {
          if (price <= 10000 && price >= this.priceRange[0]) {
            this.priceRange = [this.priceRange[0], price]
            this.params.maxSalePrice = price
          }
        } else {
          this.params.maxSalePrice = null
        }
      },
      onPriceInputChange (price) {
        price = parseInt(price)
        if (price) {
          this.priceRange = price
        }
        this.params.minSalePrice = price
      },
      sortBy: function (name) {
        this.selected = name
      },
      toggleLayout: function (value) {
        window.localStorage.setItem('layout', value)
        this.toggle = value
      },
      setActive: function (value) {
        if (value != this.status) {
          this.status = value;
        }
      },
      getBargainType (field) {
        this.params.type = field.value
      },
      getBargainField (field) {
        this.params.type = field
      },
      getCategory (field) {
        if (this.checkedCategory.indexOf(field.id) === -1) {
          this.getCategoryHelper(field.children)
        } else {
          this.getDeselectCategoryHelper(field.children)
        }
        this.params.categories = this.checkedCategory
      },
      getParentCategoryHelper (obj) {
        if (_.isEmpty(obj)) {
          return
        }
        var item = _.find(this.category, (num) => {
          return obj === num.parentId
        })
        if (_.isEmpty(item)) {
          return
        } else {
          if (this.checkedCategory.indexOf(item.id) === -1) {
            this.checkedCategory.push(item.id)
          }
          this.getParentCategoryHelper(item.parentId)
        }
      },
      getCategoryHelper (obj) {
        if (obj === null || obj === undefined) {
          return
        }
        for (var i = 0; i < obj.length; i++) {
          var item = obj[i]
          if (this.checkedCategory.indexOf(item.id) === -1) {
            this.checkedCategory.push(item.id)
          }
          this.getCategoryHelper(item.children)
        }
      },
      getDeselectCategoryHelper (obj) {
        if (obj === null || obj === undefined) {
          return
        }
        for (var i = 0; i < obj.length; i++) {
          var item = obj[i]
          var index = this.checkedCategory.indexOf(item.id)
          if (index !== -1) {
            this.checkedCategory.splice(index, 1)
          }
          this.getDeselectCategoryHelper(item.children)
        }
      },
      handleScroll () {
        this.scrolled = window.scrollY > 100
      },
      handleResize () {
        this.offsetWidth = $('.deal_body').outerWidth() +  $('.deal_body').offset().left
      }
    },
    mounted () {
      $(window).on('scroll.btn-top', this.handleScroll)
      $(window).on('resize.btn-top', this.handleResize)
      apiService.getCategoryTree().then((response) => {
        this.category = categoryFormat(response.body, this.category)
      })
      this.offsetWidth = $('.deal_body').outerWidth() +  $('.deal_body').offset().left
      $(document).ready(function () {
        // Custom
        var stickyToggle = function (sticky, stickyWrapper, scrollElement) {
          var stickyHeight = sticky.outerHeight();
          var stickyTop = stickyWrapper.offset().top;
          if (scrollElement.scrollTop() >= stickyTop) {
            stickyWrapper.height(stickyHeight);
            sticky.addClass("is-sticky");
            $('.dynamic-search').addClass("col-lg-4");
            $('.dynamic-nav').removeClass("zIndex");
            $('.dynamic-nav').addClass('nav-bottom');
          }
          else {
            sticky.removeClass("is-sticky");
            $('.dynamic-search').removeClass("col-lg-4");
            $('.dynamic-nav').addClass("zIndex");
            $('.dynamic-nav').removeClass('nav-bottom');
            stickyWrapper.height(0);
          }
        };
        // Find all data-toggle="sticky-onscroll" elements
        $('[data-toggle="sticky-onscroll"]').each(function () {
          var sticky = $(this);
          var stickyWrapper = $('<div>').addClass('sticky-wrapper'); // insert hidden element to maintain actual top offset on page
          sticky.before(stickyWrapper);
          sticky.addClass('sticky');

          // Scroll & resize events
          $(window).on('scroll.sticky-onscroll resize.sticky-onscroll', function () {
            stickyToggle(sticky, stickyWrapper, $(this));
          });

          // On page load
          stickyToggle(sticky, stickyWrapper, $(window));
        });
      });
    }
  }

</script>



