
export function categoryFormat (data, obj) {
  return categoryHelper(data, obj)
}

function categoryHelper (data, obj) {
  if (data === null || data === undefined) {
    return
  }
  for (var i = 0; i < data.length; i++) {
    var item = data[i]
    obj.push(item)
    categoryHelper(item.children, obj)
  }
  return obj
}



